import { createSlice } from '@reduxjs/toolkit'
import { alarmRequests } from '../requests.js';

export const getAlarm = () => async (dispatch, getState) => {
    const alarm= (await alarmRequests()).data;
    dispatch(setAlarm(alarm))
}

export const postAlarmTime = (value) => async (dispatch, getState) => {
    const time = (await alarmRequests({ time: value }, 'POST')).data;
    dispatch(setTime(time));
}

export const updateAlarm = (value) => async (dispatch, getState) => {
    const status = (await alarmRequests({ status: value }, 'PUT')).data;
    dispatch(setStatus(status));
}

export const alarmSlice = createSlice({
    name: 'alarm',
    initialState: {
        status: 'off',
        time: Date.now(),
        fading: 20,
    },
    reducers: {
        setAlarm: (state, action) => state = action.payload,
        setTime: (state, action) => {
            state.time = action.payload;
            state.status = 'on'
        },
        setFading: (state, action) => {
            state.fading = action.payload;
        },
        setStatus: (state, action) => {
            state.status = action.payload;
        }
    }
}
)

export const { setTime, setFading, setStatus, setAlarm } = alarmSlice.actions

export default alarmSlice.reducer