import { createSlice } from '@reduxjs/toolkit'
import { timerRequests } from '../requests.js';

export const getSleepTimer = () => async (dispatch, getState) => {
    const timer = (await timerRequests()).data;
    dispatch(setTimer(timer))
}

export const postSleepTimer = (value) => async (dispatch, getState) => {
    const time = (await timerRequests({ time: value*60000 }, 'POST')).data;
    dispatch(setSleepTime(time));
}

export const updateSleepTimer = (value) => async (dispatch, getState) => {
    const status = (await timerRequests({ status: value }, 'PUT')).data;
    dispatch(setTimerStatus(status));
}

export const timerSlice = createSlice({
    name: 'time',
    initialState: {
        status: 'off',
        time: 30,
        fading: 10,
    },
    reducers: {
        setTimer: (state, action) => {
            const {status, time, fading} = action.payload;

            state = {
                status,
                fading,
                time: time / 60000
            }
        },
        setSleepTime: (state, action) => {
            state.time = action.payload / 60000;
            state.status = 'on'
        },
        setTimerFading: (state, action) => {
            state.fading = action.payload;
        },
        setTimerStatus: (state, action) => {
            state.status = action.payload;
        }
    }
}
)

export const { setTimer, setSleepTime, setTimerFading, setTimerStatus} = timerSlice.actions

export default timerSlice.reducer