import React from 'react';
import { Grid, makeStyles } from '@material-ui/core'
import SleepTimerOnOff from './sleepTimerOnOff'
import SleepTimerTime from './sleepTimerTime';

const useStyles = makeStyles( () => ({
  heading: {
    textAlign: 'center',
    padding: '10px',
    fontSize: '1.5rem',
    fontWeight: 'bold',
  }
}));

export default function SleepTimerController() {
  const classes = useStyles();

  return (
        <Grid container>
          <Grid item xs={12} className={classes.heading}>
            Sleep Timer
          </Grid>
          <Grid item xs={4}>
            <SleepTimerOnOff />
          </Grid>
          <Grid item xs={8}>
            <SleepTimerTime />
          </Grid>
        </Grid>
  );
}
